.header {
  padding: 20px 70px;
  background-color: black;
  color: white;
  display: flex;
  text-align: center;
  justify-content: space-between;
  align-items: center;
}

.title{
  font-weight: bolder;
  font-size: 30px;
  letter-spacing: 3px;
  font-family: 'Times New Roman', Times, serif;
}

.cart-icon {
  display: flex;
}

.cart-icon .counter {
  height: 18px;
  width: 18px;
  background-color: blue;
  border-radius: 50%;
  position: relative;
  right: 8px;
  bottom: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.icon {
  font-size: 24px;
}

.cart-container {
  max-height: 100px;
}

img {
  height: 150px;
}

.cart-wrapper {
  display: inline-flex;
  margin: 40px;
  width: 320px;
  box-shadow: 0px 0px 7px #000;
  border-radius: 5px;
}

.image-wrapper {
  width: 150px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  border-right: 1px solid rgb(201, 201, 201);
}

.content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 10px;
}

.price {
  margin-bottom: auto;
}

.content button {
  background-color: black;
  color: white;
  border-radius: 3px;
}

.quantity {
  display: flex;
  align-self: center;
  border-radius: 3px;
  border: 1px solid gray;
}

.quantity div{
  height: 22px;
  width: 22px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.quantity > div:first-child,
.quantity > button {
  background-color: rgb(0, 0, 111);
  color: white;
  cursor: pointer;
  border-radius: 0;
}

.quantity > button:disabled{
  opacity: 0.7;
}

main{
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  min-height: 80vh;
  justify-content: center;
  align-items: center;
}

.total-wrapper{
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 100vw;
  background-color: gray;
  bottom: 0;
  padding: 10px;
}

.text-container{
  margin-right: 50px;
}

.total-price{
  font-weight: bolder;
}
